@import "vars";

.table {

  width: 100%;
  border-collapse: collapse;

  th {
    text-align: left;
    font-weight: normal;
  }

  th, td {
    padding: 5pt;
    border: 1pt solid black;
  }

  thead {

    tr {
      th {
        text-align: left;
      }

      th.text-end {
        text-align: right;
      }
    }

  }

  &.table-detail-view {
    width: 50%;
  }

}

.card{

  margin-top: 6pt;
  padding: 6pt 0;

  .table{
    th, td {
      padding: 5pt 0;
      border: none;
    }

    &.table-grid-view {

      th, td {
        padding: 5pt;
        border: 1pt solid $black;
      }

      thead{
        tr{
          th{
            text-align: center;
          }
        }
      }

    }

  }

}